*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}
.main_div{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgb(12, 99, 99);
}
.center_div{
  height: 80vh;
width: 25%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  box-shadow: -5px -5px 3px 0px rgb(235, 223, 223),
  5px 5px 3px 0px rgb(14, 13, 13);
  background-color: #f4f4f4;
  border-radius: 15px;;
}
.center_div h1{
  text-align: center;
  text-transform: capitalize;
  font-size: 2rem;
  color: white;
  background: transparent;
  background-color: #8566aa;
  margin-bottom: 10px;
}
.center_div input{
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  height: 30px;
  text-align: center;
  margin-left: 20px;
  border-bottom: 2px solid blueviolet;
  outline: none;
}
.center_div .mybtn{
  min-height: 40px;
  width: 50px;
  border-radius: 50%;
  background-color: #8566aa;
  
  color: #f4f4f4;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  
}
.center_div .mybtn:hover{
  background-color: aqua;
}
ol{
  margin-bottom: 30px;
}
ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;

}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.todo_style .delete{
  width:20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  
}
@media(max-width:768px){
  .center_div{
    width: 65%;
  }
  .todo_style .delete{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}